<template>
  <div >
    <div class="container p-4" >
      <div class="row">
        <div class="col-md-4" v-for="(item, index) in menu" :key="index">
          <div class="card mb-2 box-shadow post-cards" >
            <img class="card-img-top" v-bind:src="item.imageurl" width="342" height="342" >
            <div class="card-body">
              <h5 class="capitalize">{{item.nombre}}</h5>
              <p class="card-text">{{item.descripcion}}</p>
            </div>
          </div>
        </div>
      </div>
    </div> 
   </div>  
</template>
<script>
export default {
  data() {
    return {
      menu: [],
    };
  },
  created(){
    this.listarMenu();
  },
  methods:{
    listarMenu(){
      this.axios.get('/menuc')
      .then((response) => {
        // console.log(response.data)
        this.menu = response.data;
      })
      .catch((e)=>{
        console.log('error' + e);
      })
    }
  }
};
</script>