<template>
    <div > 
        <h1>CRUD</h1>
        <b-alert
            :show="dismissCountDown"
            dismissible
            :variant="mensaje.color"
            @dismissed="dismissCountDown=0"
            @dismiss-count-down="countDownChanged"
        >
        {{mensaje.texto}}
        </b-alert>

        <table class="table table-striped" >
            <td>
               <th></th> 
               <th></th>
               <th></th> 
               <th></th>
            </td>
            <td>
            <form @submit.prevent="editarProducto(ProductoEditar)" v-if="agregar">
            <h3 class="text-center">Editar Producto</h3>
            <input type="text" placeholder="Ingrese un Nombre" class="form-control my-2" v-model="ProductoEditar.nombre">
            <input type="text" placeholder="Ingrese una descripcion" class="form-control my-2" v-model="ProductoEditar.descripcion">
            <input type="text" placeholder="Ingrese url de imagen" class="form-control my-2" v-model="ProductoEditar.imageurl">
            <b-button class="btn-sm btn-block mb-1 btn-warning" type="submit">Editar</b-button>
            <b-button class="btn-sm btn-block" @click="agregar = true">Cancelar</b-button>
            </form>
            <form @submit.prevent="agregarProducto(men)" v-if="!agregar">
            <h3 class="text-center">Agregar nuevo Producto</h3>
            <input type="text" placeholder="Ingrese un Nombre" class="form-control my-2" v-model="men.nombre">
            <input type="text" placeholder="Ingrese una descripcion" class="form-control my-2" v-model="men.descripcion">
            <input type="text" placeholder="Ingrese url de imagen" class="form-control my-2" v-model="men.imageurl">
            <b-button class="btn-sm btn-block btn-success" type="submit">Agregar</b-button>
             </form>
            </td>
            <td>
               <th></th> 
               <th></th>
               <th></th> 
               <th></th>
            </td>
        </table>
       

        <table class="table table-hover">
            <thead>
                <tr>
                <th scope="col">#</th>
                <th scope="col">Nombre</th>
                <th scope="col">Descripción</th>
                <th scope="col">Imagen</th>
                <th scope="col"></th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item, index) in menu" :key="index">
                <th scope="row">{{item._id}}</th>
                <td>{{item.nombre}}</td>
                <td>{{item.descripcion}}</td>
                <td><img v-bind:src="item.imageurl" width="100" height="100"></td>
                <td>
                    <b-button class="btn-warning btn-sm mx-2" @click="activarEdicion(item._id)">Actualizar</b-button>
                    <b-button class="btn-danger btn-sm mx-2" @click="eliminarProducto(item._id)">Eliminar</b-button>
                </td>
                </tr>
            </tbody>
        </table>
  <br>
  <br>  
  <br>
  <br>
  <footer id="sticky-footer" class="py-4 bg-light text-black-50">
    <div class="container text-center">
        <h3>Desarrolladores</h3>
      <p>Jonathan Eduardo Sequen Patzan</p>
      <p>Juan Antonio Matzul Xia</p>
    </div>
  </footer>
    </div>
</template>
<script>
export default {
    data() {
    return {
      menu: [],
      mensaje: {color: 'success', texto: ''},
      dismissSecs: 5,
      dismissCountDown: 0,
      men:{nombre:'',descripcion:'',imageurl:''},
      agregar: false,
      ProductoEditar: {},
    };
  },
  created(){
    this.listarProductos();
  },
  methods:{
    listarProductos(){
      this.axios.get('/menuc')
      .then((response) => {
        // console.log(response.data)
        this.menu = response.data;
      })
      .catch((e)=>{
        console.log('error' + e);
      })
    },
    agregarProducto(){
        this.axios.post('/nuevo-objeto', this.men)
        .then(res => {
          // Agrega al inicio de nuestro array notas
          this.menu.push(res.data);

          this.men.nombre='';
          this.men.descripcion='';
          this.men.imageurl='';

          // Alerta de mensaje
          this.showAlert();
          this.mensaje.texto = 'Producto agregado!'
          this.mensaje.color = 'success';
        })
        .catch( e => {
          console.log(e.response.data.error.errors.nombre.message);

          // Alerta de mensaje
          this.showAlert();
          this.mensaje.color = 'danger';
          this.mensaje.texto = e.response.data.error.errors.nombre.message;
        })
    },
    eliminarProducto(id){
        this.axios.delete(`/menu/${id}`)
        .then(res => {
        const index = this.menu.findIndex( item => item._id === res.data._id )
        this.menu.splice(index, 1);

        this.showAlert();
        this.mensaje.texto = 'Producto Eliminado!'
        this.mensaje.color = 'danger'
        })
        .catch( e => {
        console.log(e.response);
        })
    },
     activarEdicion(id){
        this.agregar = true;
        this.axios.get(`/menu/${id}`)
        .then(res => {
            this.ProductoEditar = res.data;
        })
        .catch(e => {
            console.log(e.response);
        })
    },
    editarProducto(item){
        this.axios.put(`/menu/${item._id}`, item)
        .then(res => {
            const index = this.menu.findIndex( n => n._id === res.data._id);
            this.menu[index].nombre = res.data.nombre;
            this.menu[index].descripcion = res.data.descripcion;
            this.menu[index].imageurl = res.data.imageurl;
            
            this.showAlert();
            this.mensaje.texto = 'Producto Actualizado'
            this.mensaje.color = 'success'

        })
        .catch(e => {
            console.log(e);
        })
        this.agregar = false;
    },
    countDownChanged(dismissCountDown) {
      this.dismissCountDown = dismissCountDown
    },
    showAlert() {
      this.dismissCountDown = this.dismissSecs
    }
  }
};
</script>